import axios from 'axios';
import { WEATHER_UPDATE } from './ActionTypes'
import { lowerKeys } from '../lowerKeys';

export function fetchWeather() {
    return function (dispatch, state) {

        axios.get('http://api.infoscreen.error404.nu/Weather/Latest')
            .then(function (response) {
                const data = lowerKeys(response.data);
                dispatch({ type: WEATHER_UPDATE, payload: data })
            })
            .catch(function (error) {
                console.log(error);
            })
    }
}


