import axios from 'axios';
import { NEWS_UPDATE, NEWS_STATE } from './ActionTypes'
import { lowerKeys } from '../lowerKeys';


export function fetchNews() {
    return function (dispatch, state) {

        axios.get('http://api.infoscreen.error404.nu/News/Next')
            .then(function (response) {
                const data = lowerKeys(response.data);
                dispatch({ type: NEWS_UPDATE, payload: data })
            })
            .catch(function (error) {
                console.log(error);
            })
    }
}

export function setNewsState(newState) {
    return function (dispatch, state) {
        dispatch({ type: NEWS_STATE, payload: newState })
    }
}