import React from 'react';
import { formatImgPath } from './formatImgPath';

import { AbstractSeries } from 'react-vis';

export default class ForecastGraphIconSeries extends AbstractSeries {

    render() {
        const { data, marginLeft, marginTop, innerWidth } = this.props;

        if (!data) {
            return null;
        }

        let stepSize = innerWidth / data.length;
        let imageProps = [];
        let previousWeatherIcon = null;
        let previouswindDirectionIcon = null;

        for (let i = 0; i < data.length; i++) {

            let f = data[i];
            let wIcon = formatImgPath(f.weatherIcon);
            let dIcon = formatImgPath(f.windDirectionIcon);

            imageProps.push({
                x: i * stepSize,
                weatherIcon: previousWeatherIcon === wIcon ? null : wIcon,
                windDirectionIcon: previouswindDirectionIcon === dIcon ? null : dIcon
            });

            previousWeatherIcon = wIcon;
            previouswindDirectionIcon = dIcon;
        }

        const halfStep = (stepSize / 2);

        return (
            <g transform={`translate(${marginLeft},${marginTop})`} className="rv-xy-plot__series rv-xy-plot__series--rect">
                {imageProps.map(p => {
                    return <image x={p.x + halfStep / 2} y={-10} width={halfStep} height={halfStep} href={p.weatherIcon} />
                })}
                {imageProps.map(p => {
                    return <image x={p.x + halfStep / 2} y={115} width={halfStep} height={halfStep} href={p.windDirectionIcon} />
                })}
            </g>
        );
    }
}