import { NEWS_STATE } from '../actions/ActionTypes'

const initialState = {
    tick: 31,
    articleStyle: "article-hidden",
    progressBarStyle: "progress-value"
};

function newsReducer(state = initialState, action) {
    switch (action.type) {
        case NEWS_STATE:
            return Object.assign({}, state, action.payload);
        default:
            return state;
    }
}

export default newsReducer;