import { NEWS_UPDATE } from '../actions/ActionTypes'

const initialState = {
    title: null,
    fetched: null,
    published: null,
    image: null,
    content: null,
    source: null,
    key: null
};

function newsReducer(state = initialState, action) {
    switch (action.type) {
        case NEWS_UPDATE:
            return Object.assign({}, state, action.payload);
        default:
            return state;
    }
}

export default newsReducer;