import React from 'react';
import { connect } from 'react-redux'
import { fetchNews, setNewsState } from '../redux/actions/NewsActions';
import './News.css';
import { formatImgUrl } from './formatImgUrl';


const defaultNewsImg = '../images/no_image.png';
const articleFadeOut = "article-fade-out";
const articleFadeIn = "article-fade-in";
const progressBarBackwards = "progress-value backwards";
const progressBarForwards = "progress-value forward";
const timeOptions = { dateStyle: 'short', timeStyle: 'short' };

class News extends React.Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        this.intervalID = setInterval(() => this.tick(), 1000);
        this.tick();
    }

    componentWillUnmount() {
        clearInterval(this.intervalID);
    }

    tick() {
        const state = this.props.newsState;

        switch (state.tick) {
            case 30:
                state.articleStyle = articleFadeOut;
                state.progressBarStyle = progressBarBackwards;
                state.tick++;
                break;
            case 31:
                this.props.fetchNewsTick();
                state.tick++;
                break;
            case 32:
                state.articleStyle = articleFadeIn;
                state.tick++;
                break;
            case 33:
                state.progressBarStyle = progressBarForwards;
                state.tick = 0;
                break;
            default:
                state.tick++;
                break;
        }
        this.props.setNewsStateTick(state);
    }

    render() {

        const news = this.props.news;
        const published = news.published
            ? new Date(news.published.trimRight('Z')).toLocaleString('sv-SE', timeOptions)
            : "N/A";

        return (<div className="container-news">
            <div className={this.props.newsState.articleStyle}>
                <div className="news-title-center">
                    <h3 className="news-title">{news.title}</h3>
                </div>
                <div className="news-body">
                    <div className="content-center">
                        <div className="news-body-text">
                            <p>{news.summary}</p>
                        </div>
                    </div>
                    <img className="news-image" src={news.image ? formatImgUrl(news.image, defaultNewsImg) : defaultNewsImg} alt='article' />
                </div>
                <div className="news-footer">
                    <h4>{"Source: " + news.source}</h4>
                    <h4>{"Published at: " + published}</h4>
                </div>
            </div>
            <div className="progress-bar">
                <div className={this.props.newsState.progressBarStyle}>
                </div>
            </div>
        </div>
        );
    }
}

const mapStateToProps = state => {

    if (state.news || state.newsState) {
        return {
            news: state.news,
            newsState: state.newsState
        };
    }
    return {};
}


export default connect(
    mapStateToProps,
    {
        fetchNewsTick: fetchNews,
        setNewsStateTick: setNewsState
    }
)(News);