import React from 'react';
import { connect } from 'react-redux'
import './Weather.css';
import { fetchWeather } from '../redux/actions/weatherActions';
import WeatherForecast from './WeatherForecast';
import { formatImgUrl } from './formatImgUrl';
import { formatImgPath } from './formatImgPath';

const degree = '\u00B0';
const defaultWeatherImage = '../images/no_weather.png';
const defaultRadarImage = '../images/radarUnavailable.png';
const defaultWindDirection = '../images/North.png';

class Weather extends React.Component {

    constructor(props) {
        super(props);
        this.state = { weather: null };
    }

    componentDidMount() {
        this.intervalID = setInterval(() => this.tick(), 300000);
        this.tick();
    }

    componentWillUnmount() {
        clearInterval(this.intervalID);
    }

    tick() {
        this.props.fetchWeatherTick();
    }

    round(value) {
        return Math.round(value * 10) / 10
    }

    calcTemp(weather)
    {
        if(weather)
        {
            return this.round(weather.temperature) + ' C' + degree;
        }

        return 'N/A C' + degree;
    }

    calcTempFeelsLike(weather)
    {
        if(weather)
        {            
            if (weather.windSpeed > 35.0 || weather.temperature < -40.0 || weather.temperature > 10.0)
            {
                return '';
            }

            if(weather.windSpeed < 2.0)
            {
                return 'Feels like ' + this.round(weather.temperature) + ' C' + degree;
            }

            const windSpeedPow = Math.pow(weather.windSpeed, 0.16);
            return 'Feels like ' + this.round(13.12 + 0.6215 * weather.temperature - 13.956 * windSpeedPow + 0.48669 * weather.temperature * windSpeedPow) + ' C' + degree;
        }

        return '';
    }

    render() {
        const weather = this.props.weather;

        return (<div className="container-weather">
            <div className="container-weather-top">
                <table style={{ marginTop: '20px' }}>
                    <tbody>
                        <tr>
                            <td className="weather-icon-cell">
                                <div className="weather-icon">
                                    <img src={weather ? formatImgPath(weather.weatherIcon, defaultWeatherImage) : defaultWeatherImage} alt="weather" width={256} />
                                    <h3>{weather ? weather.weatherDescription : 'N/A'}</h3>
                                </div>
                            </td>
                            <td className="weather-info-cell">
                                <div className="content-center">
                                    <div className="weather-temp">
                                        <h2>{this.calcTemp(weather)}</h2>
                                        <h4>{this.calcTempFeelsLike(weather)}</h4>
                                    </div>
                                </div>
                                <div className="horizontal-divider"></div>
                                <div className="weather-wind-info">
                                    <div>
                                        <h4>Wind speed</h4>
                                        <h3>{weather ? this.round(weather.windSpeed) + ' m/s' : 'N/A m/s'}</h3>
                                        <h4>Wind gust speed</h4>
                                        <h3>{weather ? this.round(weather.windGustSpeed) + ' m/s' : 'N/A m/s'}</h3>
                                    </div>
                                    <img className="wind-dir-icon" src={weather ? formatImgPath(weather.windDirection, defaultWindDirection) : defaultWindDirection} alt="wind direction" />
                                </div>
                            </td>
                            <td rowSpan="2">
                                <img className="mapGif" src={weather ? formatImgUrl(weather.radarImage, defaultRadarImage) : defaultRadarImage} alt="radar" />
                            </td>
                        </tr>
                        <tr>
                            <td className="weather-info-cell">
                                <div className="horizontal-divider"></div>
                                <div style={{ paddingTop: '10px' }}>
                                    <h4>Precipitation</h4>
                                    <h3>{weather ? this.round(weather.precipitationValue.lastHour) + ' mm' : 'N/A mm'}</h3>
                                    <h4>Humidity</h4>
                                    <h3>{weather ? this.round(weather.humidity) + ' %' : 'N/A %'}</h3>
                                </div>
                            </td>
                            <td className="weather-info-cell">
                                <div className="horizontal-divider"></div>
                                <div style={{ paddingTop: '10px' }}>
                                    <h4>visibility</h4>
                                    <h3>{weather ? this.round(weather.visibility) + ' km' : 'N/A km'}</h3>
                                    <h4>cloud cover</h4>
                                    <h3>{weather ? this.round(weather.cloudCover) + ' %' : 'N/A %'}</h3>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div className="tempdiagram">
                <WeatherForecast weather={weather} />
            </div>
        </div>
        );
    }
}

const mapStateToProps = state => {

    if (state.weather) {
        return { weather: state.weather };
    }
    return {};
}


export default connect(
    mapStateToProps,
    {
        fetchWeatherTick: fetchWeather
    }
)(Weather);