import { WEATHER_UPDATE } from '../actions/ActionTypes'

const initialState = {
    weatherIcon: '../images/no_weather.png',
    weatherDescription: null,
    temperature: null,
    windSpeed: null,
    windGustSpeed: null,
    windDirection: '../images/North.png',
    radarImage: null,
    precipitationValue: {
        lastHour: null,
        last3Hours: null,
        last12Hours: null,
        last24Hours: null
    },
    humidity: null,
    visibility: null,
    cloudCover: null,
    weatherForecast: {
        minTemp: null,
        maxTemp: null,
        minWindSpeed: null,
        maxWindSpeed: null,
        startTime: null,
        endTime: null,
        forecast: []
    }
};

function weatherReducer(state = initialState, action) {
    switch (action.type) {
        case WEATHER_UPDATE:
            return Object.assign({}, state, action.payload);
        default:
            return state;
    }
}

export default weatherReducer;