import React from 'react';
import { XYPlot, XAxis, YAxis, HorizontalGridLines, VerticalGridLines, LineSeries, VerticalRectSeries } from 'react-vis';
import ForecastGraphIconSeries from './ForecastGraphIconSeries';
import defaultForecastImage from '../images/no_forecast.png';

const timeOptions = { hour12: false, timeStyle: 'short' };
const forecastYSteps = [1, 2, 3, 5];
const tickValues = [0, 0.25, 0.5, 0.75, 1]

class WeatherForecast extends React.Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    getStepMinMax(min, max) {
        let delta = max - min;
        let yCount = delta / 4;
        let stepSize = Math.ceil(yCount);

        for (let i = 0; i < forecastYSteps.length; i++) {
            if (forecastYSteps[i] >= yCount) {
                stepSize = forecastYSteps[i];
                break;
            }
        }

        return {
            stepMax: Math.ceil(max / stepSize) * stepSize,
            stepMin: Math.floor(min / stepSize) * stepSize
        }
    }

    render() {

        if (!this.props.weather || this.props.weather.weatherForecast.forecast.length === 0)
            return (<img src={defaultForecastImage} alt="weather forecast" />);

        const weatherForecast = this.props.weather.weatherForecast;

        var forecast = Object.entries(weatherForecast.forecast).map(([key, value]) => value).slice(0, 36);

        const tempMax = weatherForecast.maxTemp;
        const tempMin = weatherForecast.minTemp;
        const windMax = weatherForecast.maxWindSpeed;
        const precipitationMax = 8;
        const precipitationMin = 0.0;

        const { stepMax: tempStepMax, stepMin: tempStepMin } = this.getStepMinMax(tempMin, tempMax);
        const { stepMax: windStepMax, stepMin: windStepMin } = this.getStepMinMax(0, windMax);


        const remainder = forecast.length % 3;
        let precipitationbars = [];

        for (let i = 0; i < (forecast.length - remainder); i += 3) {
            const f1 = forecast[i];
            const f2 = forecast[i + 1];
            const f3 = forecast[i + 2];
            const totalPrecipitation = f1.precipitation + f2.precipitation + f3.precipitation;

            const startTime = new Date(f1.validTime.trimRight('Z'));
            const endTime = new Date(f3.validTime.trimRight('Z'));
            endTime.setMinutes(55);

            precipitationbars.push({
                precipitation: totalPrecipitation,
                startTime: startTime,
                endTime: endTime,
                windDirectionIcon: f2.windDirectionIcon,
                weatherIcon: f2.weatherIcon
            });
        }

        const forecastGraphData = forecast.length > 0 ? [...forecast] : [];

        if(forecastGraphData.length > 0){
            const last = forecastGraphData[forecastGraphData.length - 1];
            const tmp = new Date(last.validTime.trimRight('Z'));
            tmp.setHours(tmp.getHours() + 1);
            last.validTime = tmp.toUTCString();
            forecastGraphData.push(last);
        }        

        return (<XYPlot width={960} height={200} xType="time" yDomain={[0, 1]} margin={{ left: 70, right: 40 }}>
            <VerticalGridLines style={{ strokeWidth: 0.2 }}   tickValues={precipitationbars.map(pb => pb.startTime)}/>
            <HorizontalGridLines style={{ strokeWidth: 0.2 }} tickValues={tickValues} />
            <VerticalRectSeries
                style={{ fill: '#6495ED', stroke: 'none' }}
                data={(() => {
                    return precipitationbars
                        .map((f) => {

                            return {
                                x0: f.startTime,
                                x: f.endTime,
                                y: (f.precipitation - precipitationMin) / (precipitationMax - precipitationMin) + 0.01
                            }
                        });
                })()} />
            <LineSeries
                data={(() => {
                    return forecastGraphData.map((f) => {

                        return {
                            x: new Date(f.validTime.trimRight('Z')),
                            y: (f.temperature - tempStepMin) / (tempStepMax - tempStepMin)
                        }
                    });
                })()}
                style={{ strokeLinejoin: "round", strokeWidth: 2, fill: 'none' }}
                color="red"
                curve={"curveMonotoneX"} />
            <LineSeries
                data={(() => {
                    return forecastGraphData.map((f) => {

                        return {
                            x: new Date(f.validTime.trimRight('Z')),
                            y: (f.windSpeed - windStepMin) / (windStepMax - windStepMin)
                        }
                    });
                })()}
                style={{ strokeLinejoin: "round", strokeWidth: 2, fill: 'none' }}
                color='#FF8C00'
                curve={"curveMonotoneX"} />
            <ForecastGraphIconSeries data={precipitationbars} />
            <XAxis
                style={{
                    ticks: { fill: '#ffffff' },
                    text: { stroke: 'none', fill: '#A2A0A0', fontWeight: 400 },
                    line: { visibility: 'hidden' }
                }}
                tickValues={precipitationbars.map(pb => pb.startTime)}
                tickFormat={v => `${v.toLocaleTimeString('sv-SE', timeOptions)}`}
                tickSize={3} />
            <YAxis
                style={{
                    line: { visibility: 'hidden' }
                }}
                tickValues={tickValues}
                tickFormat={v => {
                    return (
                        <g>
                            <text text-anchor="end" dy="0.32em" transform="translate(-6, 0)" class="rv-xy-plot__axis__tick__text" style={{ fill: 'red', stroke: 'none', fontWeight: 400 }}>
                                {Math.round(v * (tempStepMax - tempStepMin) + tempStepMin)}°
                        </text>
                            <text text-anchor="end" dy="0.32em" transform="translate(-30, 0)" class="rv-xy-plot__axis__tick__text" style={{ fill: '#FF8C00', stroke: 'none', fontWeight: 400 }}>
                                {(v * (windStepMax - windStepMin) + windStepMin).toFixed(1)} m/s
                        </text>
                        </g>)
                }}
                tickPadding={1}
                tickSize={3} />
            <YAxis
                style={{
                    line: { visibility: 'hidden' }
                }}
                orientation='right'
                tickValues={tickValues}
                tickSize={3}
                tickFormat={v => {
                    return (
                        <text text-anchor="start" x="0.5em" dy="0.32em" class="rv-xy-plot__axis__tick__text" style={{ fill: '#6495ED', stroke: 'none', fontWeight: 400 }}>
                            {Math.round(v * (precipitationMax - precipitationMin) + precipitationMin)} mm
                        </text>)
                }} />
        </XYPlot>)
    }
}

export default WeatherForecast;